.category-swiper-container {
  margin-bottom: 40px;
}
.category-swiper-container .swiper {
  transition: height 0.3s ease;
}
.category-swiper-container .post-thumbnail {
  margin-bottom: 15px;
  overflow: hidden;
  border-radius: 5px;
}
.category-swiper-container .post-thumbnail img {
  width: 100%;
  height: auto;
  transition: transform 0.3s ease;
}
.category-swiper-container .post-thumbnail img:hover {
  transform: scale(1.05);
}
.category-swiper-container .post-excerpt {
  font-size: 14px;
  color: #666;
  margin-bottom: 10px;
}
.category-swiper-container .post-excerpt .truncated-excerpt {
  position: relative;
  height: 80px;
  overflow: hidden;
}
.category-swiper-container .post-excerpt .truncated-excerpt span {
  display: block;
  line-height: 1.5;
}
.category-swiper-container .post-excerpt .truncated-excerpt .excerpt-gradient {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 30px;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, rgb(255, 255, 255) 90%);
  pointer-events: none;
}
.category-swiper-container .swiper-button-next,
.category-swiper-container .swiper-button-prev {
  transition: opacity 0.3s ease;
}
.category-swiper-container .swiper-button-next.loading,
.category-swiper-container .swiper-button-prev.loading {
  opacity: 0.5;
}
.category-swiper-container .swiper-button-next.loading:after,
.category-swiper-container .swiper-button-prev.loading:after {
  content: "...";
  animation: loading-dots 1.5s infinite;
}

@keyframes loading-dots {
  0% {
    opacity: 0.2;
  }
  20% {
    opacity: 1;
  }
  100% {
    opacity: 0.2;
  }
}
/* Стилизация кнопок навигации Swiper */
.swiper-button-next,
.swiper-button-prev {
  /* Основные размеры и форма */
  width: 50px !important;
  height: 50px !important;
}

/* Стилизация самих стрелок (иконок) */
.swiper-button-next:after,
.swiper-button-prev:after {
  /* Уменьшаем размер стрелок */
  font-size: 18px !important;
  /* Меняем цвет стрелок на черный */
  color: var(--bs-dark);
  /* Удаляем отступы, чтобы стрелка была по центру круга */
  margin: 0;
}

